import React, { useState } from 'react'
import Home from '../src/Home'
import User from '../src/Users/User';
import Listings from '../src/Listings/Listings'
import UpdateUserForm from '../src/Users/UpdateUserForm'
import LoginPage from '../src/Users/LoginPage'
import SignupPage from '../src/Users/SignupPage'
import { Route, Switch } from 'react-router-dom'
import Navigation from '../src/Navigation'
import Cart from '../src/Transactions/Cart'
import { ShoeHavenApi as api } from '../src/ShoeHavenApi'
import 'bootstrap/dist/css/bootstrap.min.css';
import CreateCommentForm from './Comments/CreateComment';
import CreateListingForm from './Listings/CreateListingForm'
import OrderCancel from './Transactions/OrderCancel'
import OrderPlaced from './Transactions/OrderPlaced'
import Comments from './Comments/Comments'

function App() {
  //user auth or profile functions
  const [userLoggedIn, setUserLoggedIn] = useState(localStorage.getItem('username'))

  async function signup(username, password, firstname, lastname, email) {
    await api.signup(username, password, firstname, lastname, email)
    setUserLoggedIn(localStorage.getItem('username'))
    return new Promise((resolve, reject) => {
      resolve()
    })
  }

  async function login(username, password) {
    await api.login(username, password)
    setUserLoggedIn(localStorage.getItem('username'))
    return new Promise((resolve, reject) => {
      resolve()
    });
  }

  function logout() {
    localStorage.clear()
    alert('Logged Out')
    setUserLoggedIn(localStorage.getItem('username'))
  }

  async function updateUser(password, firstname, lastname, email) {
    await api.updateUser(password, firstname, lastname, email)
  }

  //submitting feedback
  async function addComment(body) {
    await api.request(`comments/new`, { body }, 'post')
    alert('Successfully Submitted. Thank You For Your Feedback')
  }

  //adding a listing
  async function addListing(title, picture, price, details) {
    await api.request(`listings/new`, { title, picture, price: parseFloat(price), details, sold: false }, 'post')
  }
  async function deleteListing(id) {
    await api.request(`listings/${id}`, {}, 'delete')
  }

  //cart functions
  const [cart, setCart] = useState([])
  const [successfulCart, setSuccessfulCart] = useState([])

  function addToCart(cartItem) {
    setCart(cart => (
      [
        ...cart,
        cartItem
      ]
    ))

    alert('Added To Cart')
  }

  function removeFromCart(id) {
    setCart(
      cart.filter(cartItem => (cartItem['id'] !== id))
    )
  }

  async function checkout() {
    let checkout = await api.request(`sales/checkout`, { cart }, 'post')
    setSuccessfulCart(checkout)
    setCart([])
    return checkout
  }

  function resetSuccessfulCart(){
    setSuccessfulCart([])
  }

  return (
    <div className="App">
      <Navigation logout={logout} userLoggedIn={userLoggedIn} />
      <Switch>
        <Route exact path='/'>
          <Home userLoggedIn={userLoggedIn} />
        </Route>

        <Route exact path='/login'>
          <LoginPage login={login} />
        </Route>

        <Route exact path='/comments'>
          <Comments />
        </Route>

        <Route exact path='/signup'>
          <SignupPage signup={signup} />
        </Route>

        <Route exact path='/listings'>
          <Listings addToCart={addToCart} deleteListing={deleteListing} />
        </Route>

        <Route exact path='/listings/new'>
          <CreateListingForm addListing={addListing} />
        </Route>

        <Route exact path='/users/:username'>
          <User />
        </Route>

        <Route exact path='/:username/update'>
          <UpdateUserForm updateUser={updateUser} />
        </Route>

        <Route exact path='/cart'>
          <Cart items={cart} removeFromCart={removeFromCart} checkout={checkout}/>
        </Route>

        <Route exact path='/feedback'>
          <CreateCommentForm addComment={addComment} />
        </Route>

        <Route exact path='/orderPlaced'>
          <OrderPlaced successfulCart={successfulCart} resetSuccessfulCart={resetSuccessfulCart}/>
        </Route>

        <Route exact path='/orderCanceled'>
          <OrderCancel resetSuccessfulCart={resetSuccessfulCart}/>
        </Route>

      </Switch>
    </div>
  );
}

export default App;
