import React, { useEffect } from 'react'
import {Button} from 'react-bootstrap'
import {Link} from 'react-router-dom'

const OrderCancel = ({resetSuccessfulCart}) => {
    useEffect(() => {
        function eraseOldCart(){
            resetSuccessfulCart()
        }
        eraseOldCart()
    },[resetSuccessfulCart])

    return(
        <div>
            <h1>Order Canceled</h1>
            <Button as={Link} to='/listings'>Return To Listings</Button>
        </div>
    )
}

export default OrderCancel