import React, { useEffect, useState } from 'react'
import { Card, Button, Row, Col } from 'react-bootstrap'
import {ShoeHavenApi as api} from '../ShoeHavenApi'

const Listing = ({ id, addToCart, deleteListing}) => { 
    const [listing, setListing] = useState({})
    useEffect(() => {
        async function getListing(){
            let res = await api.request(`listings/${id}`)
            setListing(res)
        }
        getListing()
    },[id]) 

    return (
        <div>
            <Card style={{ width: '18rem' }}>
                <Card.Img variant="top" src={listing.picture} alt='Cool Listing' />
                <Card.Body>
                    <Card.Title>{listing.title}</Card.Title>
                    <Card.Text>{listing.details}</Card.Text>
                    <Card.Text>Price: ${listing.price}</Card.Text>
                    <Row>
                        <Button as={Col} onClick={() => addToCart({ 
                            id:listing.id,
                            price:listing.stripe_price_id,
                            quantity:1
                        })} variant="success">
                            Add To Cart
                        </Button>
                        {(localStorage.getItem('isAdmin') === 'true') &&
                            <Button as={Col} onClick={() => deleteListing(id)}
                                variant='danger'>
                                Delete Listing
                        </Button>}
                    </Row>
                </Card.Body>
            </Card>
        </div>
    )
}


export default Listing