import React, {useState, useEffect} from 'react'
import {Card, Button} from 'react-bootstrap'
import {ShoeHavenApi as api} from '../ShoeHavenApi'

const CartItem = ({ id, removeFromCart }) => {
    const [cartItem, setCartItem] = useState({})

    useEffect(() => {
        async function pullDetails(){
            let res = await api.request(`listings/${id}`)
            setCartItem(res)
        }
        pullDetails()
    },[id])

    return (
        <div>
            <Card style={{ width: '18rem' }}>
                <Card.Img variant="top" src={cartItem.picture} alt='Cool Listing' />
                <Card.Body>
                    <Card.Title>{cartItem.title}</Card.Title>
                    <Card.Text>${cartItem.price}</Card.Text>
                    <Button variant='warning' onClick={() => removeFromCart(id)}> Remove From Cart </Button>
                </Card.Body>
            </Card>
        </div>
    )
}

export default CartItem