import React from 'react'
import CartItem from './CartItem'
import { v4 as uuid } from 'uuid'
import { Button } from 'react-bootstrap'

const Cart = ({ items, removeFromCart, checkout }) => {       

    return (
        <div>
            <h1>Cart:</h1>
            {items.map((cartItem) => (
                <CartItem
                    key={uuid()}
                    id={cartItem.id}
                    removeFromCart={removeFromCart}
                />
            ))}

            <br></br><br></br>
            {(items.length > 0) ? <Button onClick={() => checkout()} variant='success'>Checkout</Button> 
            : <h1>Cart Is Empty!</h1>}
        </div>
    )

}

export default Cart