import React, { useEffect } from 'react'
import {Button} from 'react-bootstrap'
import {Link} from 'react-router-dom'
import {ShoeHavenApi as api} from '../ShoeHavenApi'

const OrderPlaced = ({successfulCart, resetSuccessfulCart}) => {
    useEffect(() => {
        async function recordSaleInternally(){
            await api.request(`sales/new`,{successfulCart}, 'post')
            resetSuccessfulCart()
        }
        recordSaleInternally()
    },[successfulCart, resetSuccessfulCart])
    
    return(
        <div>
            <h1>Order Placed</h1>
            <Button as={Link} to='/listings'>Return To Listings</Button>
        </div>
    )
}

export default OrderPlaced