import React, { useEffect, useState } from 'react'
import { ShoeHavenApi as api } from '../ShoeHavenApi'
import { Card, Button } from 'react-bootstrap'

const Purchase = ({ id, returnItem }) => {
    const [purchase, setPurchase] = useState({})

    useEffect(() => {
        async function getPurchase() {
            let res = await api.request(`listings/${id}`)
            setPurchase(res)
        }
        getPurchase()
    }, [id])

    return (
        <Card>
            <Card.Header>
                Item: {purchase.title}
            </Card.Header>
            <Card.Body>
                <img src={purchase.picture} alt="item you purchased"/>
            </Card.Body>
            <Button onClick={async () => await returnItem(purchase.id)}>
                Return This Item
            </Button>
        </Card>
    )
}

export default Purchase