import React, { useEffect, useState } from 'react'
import { ShoeHavenApi as api } from '../ShoeHavenApi'
import { Link, useParams, useHistory } from 'react-router-dom'
import { Card, Button, Row, Col, Container } from 'react-bootstrap'
import Purchase from '../Transactions/Purchase'
import {v4 as uuid} from 'uuid'

const User = () => {
   let {username} = useParams()
   let history = useHistory()

    const [profile, setProfile] = useState([])
    const [purchases, setPurchases] = useState([])

    useEffect(() => {
        async function profile() {
            let res = await api.request(`users/${username}`)
            setProfile(res)
        }
        profile()
    }, [username])

    useEffect(() => {
        async function getPurchases() {
            let res = await api.request(`users/${username}/purchases`)
            setPurchases(res.filter((purchase) => purchase.returned === false))
        }
        getPurchases()
    }, [username])

    async function returnItem(saleId){
        await api.request(`sales/return/${saleId}`,{},'post')
        alert('You Have Returned The Item')
        history.push('/listings')
    }

    return (
        <div>
            <Container>
                <Row xs={3}>
                    <Card as={Col} style={{ width: '500px' }}>
                        <h1>Welcome {username} !</h1>
                        <Card.Title> Your Profile:</Card.Title>
                        <Card.Body>
                            <h6>Name: {profile.firstname} {profile.lastname}</h6>
                            <h6>Email: {profile.email}</h6>
                            <br></br><br></br>
                        </Card.Body>
                        <Card.Footer>
                            <Button variant='primary' as={Link} to={`/${username}/update`}>Update Your Profile </Button>
                        </Card.Footer>
                    </Card>
                    <Card as={Col} style={{ width: '500px' }}>
                        <Card.Title> Your Purchases: </Card.Title>
                        <Card.Body>
                            <div>
                                {purchases.map((purchase) => (
                                    <Purchase key={uuid()} id={purchase.id} returnItem={returnItem}/>
                                ))}
                            </div>
                        </Card.Body>
                    </Card>
                </Row>
            </Container>
        </div>
    )
}

export default User